* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'PlayFair Display';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.search-results {
  text-align: center;
  margin: 2%;
}

#input {
  font-family: inherit;
  font-size: 1.5em;
  text-align: center;
  font-style: italic;
  outline: none;
}

.articles {
  display: grid;
  grid-gap: 3em;
  padding: 2em;
}

.card a, h2 {
  color: #32444b;
}

.card h3 {
  color: #667074;
}

input:focus::placeholder {
  color: transparent;
}

header {
  text-align: center;
  margin: 1%;
  font-size: 5em;
  border-bottom: 3px solid rgb(249, 236, 101);
}

a {
  font-family: inherit;
  font-size: 2em;
  color: inherit;
  text-decoration: inherit;
}

img {
  display: flex;
  max-width: 100%;
  margin: 1em auto;
}

@media (max-width: 500px) {
  .articles {
    grid-template-columns: 1fr;
  }
}

@media (min-width: 501px) {
  .articles {
    grid-template-columns: 2fr 1fr;
  }
}

@media (min-width: 800px) {
  .articles {
    grid-template-columns: repeat(auto-fill, minmax(15%, 1fr));
  }
}

